import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)
import Main from '@/pages/Main'
import Dashboard from '@/pages/Dashboard'
import Member from '@/pages/Member'

let routers=[{
  path: '/',
  component: Main,
  hidden:true,
  children:[{
    path:'/',
    component:Dashboard,
    name: '首页'
  }]
}]
routers.push({
  path:'/member',
  name:'会员管理',
  component:Main,
  icoCls:'fa fa-user-circle-o',
  children:[{
    path:'/member/data',
    component:Member,
    name:'会员信息管理',
  }]
})

const router=new Router({
  routes:routers
})

export default router;



